 solid #3b6f7f}.oficinas-card__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#000;margin-bottom:.5rem}.oficinas-card__text{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;line-height:1.6;margin-bottom:.5rem}.oficinas-card__specs{background-color:#f5f5f5;border-radius:4px;padding:.5rem;margin-bottom:1rem}.oficinas-card__specs p{font-family:"Hind",sans-serif;font-size:clamp(12px,1.2vw,14px);color:#333;margin-bottom:.25rem}.oficinas-card__specs p:last-child{margin-bottom:0}.oficinas-card__specs p strong{color:#000}.oficinas-card__price{font-family:"Outfit",sans-serif;font-size:clamp(20px,3vw,32px);font-weight:700;color:#3b6f7f;margin-bottom:.5rem}.oficinas-card__includes{font-family:"Hind",sans-serif;font-size:clamp(12px,1.2vw,14px);color:#333;background-color:rgba(59,111,127,.05);border-radius:4px;padding:.5rem;margin-bottom:.5rem}.oficinas-card__includes strong{display:block;margin-bottom:.25rem;color:#000}.oficinas-card__includes ul{margin:0;padding-left:1rem}.oficinas-card__includes ul li{margin-bottom:2px}.oficinas-card__includes ul li:last-child{margin-bottom:0}.oficinas-card__disclaimer{font-family:"Hind",sans-serif;font-size:clamp(12px,1.2vw,14px);color:#ccc;font-style:italic;display:block}.oficinas-card__result{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;padding-top:.5rem;border-top:1px solid #ccc;margin-top:.5rem}.oficinas-equip{background-color:#fff;border-radius:8px;padding:1rem;height:100%;border-left:4px solid #3b6f7f}.oficinas-equip__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#3b6f7f;margin-bottom:.5rem}.oficinas-equip__list{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;list-style:none;padding-left:0;margin:0}.oficinas-equip__list li{padding-left:1rem;position:relative;margin-bottom:.25rem}.oficinas-equip__list li:before{content:"✓";position:absolute;left:0;color:#3b6f7f;font-